*{
    padding:0 0 0 0;
    margin:0 0 0 0;
}
body{
    min-height: 100vh;
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
}

#quote-box{
    width: 50vw;
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#text{
    width:80%;
    min-height: 10vh;
    padding: 2vw;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

#author{
    width:80%;
    padding: 2vw;
    text-align: end;
    margin-bottom: 2vh;
}
#boy{
    display: flex;
    width :100%;
    padding-left: 5vw;
}

#tweet-quote{
    width:45%;
}
#new-quote{
    width:45%;
    cursor: pointer;

}